import PropTypes from "prop-types";
import { useState } from "react";
import { format } from "date-fns";
// @mui
import { useTheme } from "@mui/material/styles";
import {
  TableRow,
  Checkbox,
  TableCell,
  Typography,
  MenuItem,
} from "@mui/material";
// utils
import { fCurrency } from "../../../../utils/formatNumber";
// components
import Iconify from "../../../../components/Iconify";
import { TableMoreMenu } from "../../../../components/table";
import { useTranslation } from "react-i18next";
import Label from "../../../../components/Label";
//

// ----------------------------------------------------------------------

FeeTableRow.propTypes = {
  row: PropTypes.object,
  selected: PropTypes.bool,
  onEditRow: PropTypes.func,
  onSelectRow: PropTypes.func,
  onDeleteRow: PropTypes.func,
  deleteText: PropTypes.string,
  editText: PropTypes.string,
};

export default function FeeTableRow({
  row,
  selected,
  onEditRow,
  onSelectRow,
  onDeleteRow,
  deleteText = "Delete",
  editText = "Edit",
}) {
  const theme = useTheme();

  const { t } = useTranslation();

  const [openMenu, setOpenMenuActions] = useState(null);

  const handleOpenMenu = (event) => {
    setOpenMenuActions(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setOpenMenuActions(null);
  };

  return (
    <TableRow hover selected={selected}>
      <TableCell padding="checkbox">
        <Checkbox checked={selected} onClick={onSelectRow} />
      </TableCell>

      <TableCell padding="checkbox" />

      <TableCell align="left">
        {t(`type_transaction.${row.type_transaction}`)}
      </TableCell>
      <TableCell align="center">{row.currency}</TableCell>
      <TableCell align="center">
        <Typography variant="subtitle2">{row.daily_limit}</Typography>
        <Typography variant="body2" sx={{ color: "text.secondary" }}>
          {fCurrency(row.daily_amount)}
        </Typography>
      </TableCell>
      <TableCell align="center">
        <Typography variant="subtitle2">{row.weekly_limit}</Typography>
        <Typography variant="body2" sx={{ color: "text.secondary" }}>
          {fCurrency(row.weekly_amount)}
        </Typography>
      </TableCell>
      <TableCell align="center">
        <Typography variant="subtitle2">{row.monthly_limit}</Typography>
        <Typography variant="body2" sx={{ color: "text.secondary" }}>
          {fCurrency(row.monthly_amount)}
        </Typography>
      </TableCell>
      <TableCell align="center">
        <Label
          variant={theme.palette.mode === "light" ? "ghost" : "filled"}
          color={!row.is_activate ? "error" : "success"}
          sx={{ textTransform: "capitalize" }}
        >
          {row.is_activate ? t("params.yes") : t("params.no")}
        </Label>
      </TableCell>

      <TableCell align="center">
        <Typography variant="subtitle2">
          {format(new Date(row.last_updated), "dd MMM yyyy")}
        </Typography>
        <Typography variant="body2" sx={{ color: "text.secondary" }}>
          {format(new Date(row.last_updated), "p")}
        </Typography>
      </TableCell>

      <TableCell align="right">
        <TableMoreMenu
          open={openMenu}
          onOpen={handleOpenMenu}
          onClose={handleCloseMenu}
          actions={
            <>
              <MenuItem
                onClick={() => {
                  handleCloseMenu();
                  onDeleteRow();
                }}
                sx={{ color: "error.main" }}
              >
                <Iconify icon={"eva:trash-2-outline"} />
                {deleteText}
              </MenuItem>
              <MenuItem
                onClick={() => {
                  onEditRow();
                  handleCloseMenu();
                }}
              >
                <Iconify icon={"eva:edit-fill"} />
                {editText}
              </MenuItem>
            </>
          }
        />
      </TableCell>
    </TableRow>
  );
}
